.App {
  text-align: center;
}

.divTable{
  display:table;
  max-width: 20em;
  float:right;
  display:flex;
  align-items:center;
  font-family: 'Press Start 2P', cursive;
}

.divTableRow{
  display:table-row;
}

.divTableHeading{
  background-color: #eee;
  display: table-header-group;
}

.divTableCell,
.divTableHead{
  border: 1px solid #999999;
  display: table-cell;
  padding: 3px 10px
}

.divTableHeading {
  background-color: #eee;
  display: table-header-group;
  font-weight: bold;
}

.divTableFoot{
  background-color: #eee;
  display: table-footer-group;
  font-weight: bold;
}

.divTableBody{
  display: table-row-group;
}

.divTableCell:nth-child(1n){
  background-color: gainsboro;
}

.container{
  display: flex;
  opacity: 0.9;
  background-color: #99aaab;
  justify-content: center;
}

img{
  height: 16em;
}

input{
  box-sizing: border-box;
  font-size: 2rem;
  padding: 1rem;
  display:block;
  margin: 2rem auto;
}

body{
  background-image: url("https://wallpaperaccess.com/full/418495.jpg");
  background-size: 100%;
}